<template>
  <v-dialog @click:outside="close" width="750" v-model="$store.state.dialogs.dialogs.order.update">
    <v-card>
      <v-card-title>
        <span class="text-h5 font-weight-bold"> Заказ №{{ order.id }} </span>
      </v-card-title>

      <v-card-text class="mx-0 my-0">
        <v-container :fluid="true">
          <v-row>
            <v-col cols="12">
              <h2 class="font-weight-bold h1 mb-3">
                Реквизиты заказа
              </h2>
              <v-row no-gutters>
                <v-col>Клиент:</v-col>
                <v-col>{{ order.user.information.first_name }}</v-col>
              </v-row>
            </v-col>
            <v-col cols="12">
              <v-row no-gutters>
                <v-col>Адрес:</v-col>
                <v-col>{{ order.data.address }}</v-col>
              </v-row>
            </v-col>
            <v-col cols="12">
              <v-row no-gutters>
                <v-col>Моб. телефон:</v-col>
                <v-col>{{ order.data.phone }}</v-col>
              </v-row>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <h2 class="font-weight-bold h1 mb-3">
                Статус заказа
              </h2>
            </v-col>
            <v-col cols="12">
              <v-select
                v-model="form.status"
                :items="statuses"
                item-text="name"
                return-object
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <h2 class="font-weight-bold h1 mb-3">
                Состав заказа
              </h2>
            </v-col>
            <v-col cols="12">
              <v-simple-table>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">
                        Название товара
                      </th>
                      <th class="text-left">
                        Цена
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="product in orderProducts" :key="product.name">
                      <td>{{ product.name }}</td>
                      <td>{{ product.price }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <h2 class="font-weight-bold h1 mb-3">
                Комментарий к заказу
              </h2>
            </v-col>
            <v-col cols="12">
              <v-textarea
                background-color="amber lighten-4"
                class="orange-darken-4 p-2"
                v-model="form.worker_comments"
              />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <v-checkbox
                v-model="notify"
                label="Уведомить клиента об изменении"
              />
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn @click="update" :disabled="!isChanged" color="primary">
          Обновить заказ
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "UpdateOrderDialog",
  data() {
    return {
      notify: false,
      cache: {
        status: null,
        products: [],
        worker_comments: ""
      },
      form: {
        status: null,
        products: [],
        worker_comments: ""
      }
    };
  },
  methods: {
    update() {
      this.$emit("toggleUpdate", {
        id: this.$props.order.id,
        form: this.form,
        notify: this.notify
      });

      this.cache.form = { ...this.form.products };
      this.cache.status = { ...this.form.status };
    },
    close() {
      const defaultParams = {
        status: null,
        products: [],
        worker_comments: ""
      };

      this.form = { ...defaultParams };
      this.cache = { ...defaultParams };
    }
  },
  computed: {
    orderProducts() {
      return this.order.products;
    },
    isChanged() {
      let change = false;

      if (!this.lodash.isEqual(this.form, this.cache)) {
        change = true;
      }

      return change;
    }
  },
  props: {
    order: {
      type: Object,
      required: true
    },
    statuses: {
      type: Array,
      required: true
    }
  },
  watch: {
    order: {
      immediate: true,
      handler() {
        this.cache.worker_comments = this.$props.order.worker_comments;
        this.cache.products = [...this.form.products];

        this.orderProducts.forEach(product =>
          this.form.products.push(product.id)
        );
        this.form.worker_comments = this.$props.order.worker_comments;
        this.form.status = this.cache.status = this.$props.statuses.find(
          iterator => iterator.name === this.order.status.name
        );
      }
    }
  }
};
</script>

<style scoped></style>
