<template>
  <v-data-table
    :items="orders"
    :headers="headers"
    :server-items-length="40"
    :options="{ page: 1, itemsPerPage: 10 }"
    class="elevation-4"
  >
    <template v-slot:item.id="{ item }">
      {{ item.id }}
    </template>
    <template v-slot:item.user="{ item }">
      {{ item.user.information.first_name }}
    </template>
    <template v-slot:item.status="{ item }">
      {{ item.status.name }}
    </template>
    <template v-slot:item.total_sum="{ item }">
      {{ item.total_cash + " руб." }}
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editItem(item.id)">
        mdi-pencil
      </v-icon>
      <v-icon small @click="deleteItem(item)">
        mdi-delete
      </v-icon>
    </template>
  </v-data-table>
</template>

<script>
export default {
  name: "OrdersTable",
  data() {
    return {
      headers: [
        {
          text: "Номер",
          align: "start",
          sortable: false,
          value: "id"
        },
        {
          text: "Клиент",
          sortable: false,
          value: "user"
        },
        {
          text: "Статус заказа",
          sortable: false,
          value: "status"
        },
        {
          text: "Итоговая сумма",
          sortable: false,
          value: "total_sum"
        },
        {
          text: "Действия",
          sortable: false,
          value: "actions"
        }
      ]
    };
  },
  methods: {
    editItem(id) {
      this.$emit("toggleEdit", id);
    },
    deleteItem() {}
  },
  props: {
    orders: {
      type: Array,
      required: true
    }
  }
};
</script>

<style scoped></style>
