<template>
  <v-container :fluid="true">
    <template v-if="order">
      <UpdateOrderDialog
        @toggleUpdate="changeOrder"
        :order="order"
        :statuses="statuses"
      />
    </template>
    <v-row>
      <v-col cols="12">
        <h1 class="font-weight-bold mb-3">Все заказы</h1>
        <v-breadcrumbs class="px-0 py-0 mb-3" :items="items" divider="/">
          <template v-slot:item="{ item }">
            <v-breadcrumbs-item
              :disabled="item.disabled"
              :exact="true"
              :to="{ name: item.to.name }"
            >
              {{ item.text.toUpperCase() }}
            </v-breadcrumbs-item>
          </template>
        </v-breadcrumbs>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <div class="mb-3">
          <OrdersTable @toggleEdit="showEditOrder" :orders="orders" />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";
import OrdersTable from "../../components/Tables/OrdersTable";
import UpdateOrderDialog from "../../components/Dialogs/order/UpdateOrderDialog";

export default {
  name: "Orders",
  metaInfo: {
    title: "Шестерёнка | Заказы"
  },
  data() {
    return {
      items: [
        {
          text: "Панель",
          disabled: false,
          to: { name: "Orders" }
        },
        {
          text: "Все заказы",
          disabled: false,
          to: { name: "Orders" }
        }
      ],
      dialog: false,
      selectOrder: null
    };
  },
  methods: {
    ...mapMutations({
      changeOrderInOrdersState: "updateOrderInOrdersList"
    }),
    ...mapActions({
      loadOrders: "fetchOrders",
      loadOrder: "fetchOrder",
      updateOrder: "fetchUpdateOrder",
      loadStatuses: "fetchOrderStatuses",
      showDialog: "show"
    }),
    async showEditOrder(id) {
      await this.loadOrder(id);
      this.showDialog("order:update");
    },
    async changeOrder(updateData) {
      await this.updateOrder(updateData);
      await this.loadOrders();
      this.showDialog("order:update");
    }
  },
  computed: {
    ...mapGetters({
      statuses: "getOrderStatuses",
      orders: "getOrders",
      order: "getOrder"
    })
  },
  components: {
    OrdersTable,
    UpdateOrderDialog
  },
  async beforeMount() {
    await this.loadOrders();
    await this.loadStatuses();
  }
};
</script>
